import SalaryPicker from '../../components/salaryPicker/salaryPicker.vue'
import { debounce } from '../../utils/utils.js'
import { mapGetters, mapActions} from 'vuex'
export default {
	components: {
		SalaryPicker
	},
	data() {
		return {
			filterList: [
				{
					type: 'hopePos',
					name: '期望职位类别'
				},
				{
					type: 'hopeType',
					name: '工作性质',
					typeList: ['全职','兼职','实习']
				},
				{
					type: 'hopeSalary',
					name: '期望月薪'
				},
				{
					type: 'hopeCity',
					name: '期望城市'
				},
				{
					type: 'hopeStart',
					name: '到岗时间'
				}
			]
		}
	},
	computed: {
		...mapGetters([
			'hopeSalary',
			'hopeCity',
			'hopeType'
		]),
		hopeObj() {
			return {
				hopeSalary: this.hopeSalary,
				hopeCity: this.hopeCity,
				hopeType: this.hopeType
			}
		}
	},
	methods: {
		...mapActions([
			'setHopeSalary',
			'setHopeType'
		]),
		handleClick(type, list) {
			switch(type) {
				case 'hopePos':
					console.log('goto hopePos');
					break;
				case 'hopeType':
					uni.showActionSheet({
						itemList: list,
						success: (res) => {
							let index = res.tapIndex
							this.setHopeType(list[index])
							// this.filterList.forEach((item) => {
							// 	if(item.type === type) {
							// 		item.value = list[index]
							// 	}
							// })
						}
					})
					break;
				case 'hopeSalary':
					console.log('goto hopeSalary');
					break;
				case 'hopeCity':
					uni.navigateTo({
						url: '../selectRegion/selectRegion'
					})
					break;
				case 'hopeStart':
					console.log('goto hopeStart');
					break;
			}
		},
		handleComfirm(e) {
			this.setHopeSalary(e)
		}
		
	}
}

